import React from 'react';
import { AiOutlineLike } from "react-icons/ai";

const UpvotesSection = ({ articleName, setArticleInfo, upvotes }) => {
    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`, {
            method: 'post',
        });
        const body = await result.json();
        setArticleInfo(body);
    }
    return (
        <div >
            <p>
            <button className="btn btn-primary" onClick={() => upvoteArticle()} style={{float:'left', marginRight: '10px'}}><AiOutlineLike /></button>
            <small><strong>This post has been upvoted {upvotes} times</strong></small>
            </p>
        </div>
    );
}

export default UpvotesSection;